import React, {Component} from 'react';
import Buttons from '../Buttons';
import { Link } from "react-router-dom";
import './style.css';

export default class Post extends Component {

  constructor(props) {
    super(props);
    this.state = {
      isLiked: false
    };

    this.handleClickIsLiked = this.handleClickIsLiked.bind(this);
  }

  handleClickIsLiked(isLiked) {
    this.setState((prevState) => {
      return {isLiked: !prevState.isLiked}
    });
  }

  render() {
    const {post} = this.props;
    return (
      <div className="post">
        <Link className="post__body" to={{ pathname: `/modal/${post.id}`, state: { modal: true } }}>
          <img src={post.img} alt={post.title} />
        </Link>
        <div className="post__bottom">
          <Buttons post={post}
                   isLiked={this.state.isLiked}
                   onButtonClick={this.handleClickIsLiked.bind(this)}
          />
          <div className="post__title">
            {post.title}
            <br/>
            <strong>Type: {post.type}</strong>
          </div>
        </div>
      </div>
    );
  }
}