import React, {Component, Fragment} from "react";
import { HashRouter, Switch, Route } from "react-router-dom";
import Modal from "../Modal";
import PostsList from "../PostsList";
import Share from "../Share";
import Filter from "../Filter";

export default class App extends Component {

  constructor(props) {
    super(props);
    this.state = {
      filterValue: ''
    };

    this.handleFilterValueChange = this.handleFilterValueChange.bind(this);
  }

  handleFilterValueChange(filterValue) {
    this.setState({
      filterValue: filterValue
    });
  }

  render() {
    return (
      <Fragment>
        <Filter filterValue={this.state.filterValue} onFilterValueChange={this.handleFilterValueChange}/>
        <HashRouter>
          <Switch>
            <Fragment>
              {/*<Route path="/" component={PostsList} />*/}
              <Route path="/" render = {() => (
                <PostsList filterValue={this.state.filterValue} />
              )}/>
              <Route path="/share/:id" component={Share} />
              <Route path="/modal/:id" component={Modal} />
            </Fragment>
          </Switch>
        </HashRouter>
      </Fragment>
    );
  }
}