import React, { Component, Fragment } from "react";
import { Link } from "react-router-dom";
import Helmet from "react-helmet";
import posts from "../../fixtures";
import "./style.css";

export default class Modal extends Component {
  render() {
    const modalId = this.props.match.params.id;
    const post = posts.find(x => x.id === modalId);
    const url = window.location.href;
    return (
      <Fragment>
        <Helmet
          meta={[
            {"property": "og:title", "content": post.title},
            {"property": "og:description", "content": post.text},
            {"property": "og:image", "content": post.img},
            {"property": "og:image:secure_url", "content": post.img},
            {"property": "og:url", "content": url},
          ]}
        />
        <div className="modal">
          <div className="modal__inner">
            {post ? (
              <Fragment>
                <h2 className="modal__title">{post.title}</h2>
                <div className="modal__body">
                  <img src={post.img} alt={post.title} />
                </div>
              </Fragment>
            ) : (
              <div>Not found</div>
            )}
            <Link className="modal__close" to="/">X</Link>
          </div>
        </div>
      </Fragment>
    );
  }
}