import React, { Component } from "react";
import {FacebookShareButton, PinterestShareButton, TwitterShareButton} from "react-share";
import "./style.css";

export default class Social extends Component {
  render() {
    const {url, encodeUrl, post} = this.props;
    return (
      <div className="social">
        <FacebookShareButton className="social__btn" url={encodeUrl}>Facebook</FacebookShareButton>
        <TwitterShareButton className="social__btn" url={encodeUrl} title={url}>Twitter</TwitterShareButton>
        <PinterestShareButton className="social__btn" url={encodeUrl} media={post.img} description={post.text}>Pinterest</PinterestShareButton>
        {/*<a href={"https://www.facebook.com/sharer/sharer.php?u=" + encodeUrl} target="_blank" rel="noopener noreferrer" className="social__btn social__btn--fb">Facebook</a>*/}
        {/*<a href={"https://twitter.com/intent/tweet/?url=" + encodeUrl} target="_blank" rel="noopener noreferrer" className="social__btn social__btn--tw">Twitter</a>*/}
        {/*<a href={"http://pinterest.com/pin/create/button/?url=" + encodeUrl} target="_blank" rel="noopener noreferrer" className="social__btn social__btn--pin">Pinterest</a>*/}
      </div>
    );
  }
}